import GlobalSettings from '@/GlobalSettings'
import { IKDataEntity } from 'metaflow-js'
import hillo from 'hillo'
import Utils from '../components/Utlis/Utils'

// const dateRules = [
//   v => Date.parse(v) > Date.parse(new Date().toJSON().slice(0, 10)) || 'please enter the correct price',
// ]

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    form: false,
  },
  name: {
    displayName: 'reservename',
  },
  personCount: {
    displayName: 'personCount',
  },
  phone: {},
  date: {
    type: IKDataEntity.Types.Date,
    formConfig: {
      dateLocale: GlobalSettings.getLang(),
    },
  },
  time: {
    type: IKDataEntity.Types.Time,
  },
}

const config = {
  add: function (item) {
    item.count = item.personCount
    if (Date.parse(item.date + ' ' + item.time) < Date.parse(new Date().toJSON())) {
      Utils.showError('Die Uhrzeit darf nicht vor dem aktuellen Datum liegen')
    } else {
      item.time = item.date + ' ' + item.time
      return hillo.postWithUploadFile('Reserve.php?op=add', item, {
        showLoading: true,
      })
    }
  },
  edit: function (item) {
    item.count = item.personCount
    item.time = item.date + ' ' + item.time
    return hillo.postWithUploadFile('Reserve.php?op=update', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Reserve.php?op=delete', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Reserve.php', {
      op: 'all',
      lang: GlobalSettings.getLang(),
      ...filter,
    })).content.map(function (item) {
      if (item.reserveTime) {
        item.date = item.reserveTime.substring(0, 10)
        item.time = item.reserveTime.substring(10)
      }
      return item
    })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
